<template>
	<div>
		<div class="ant-note">
			<div class="ant-note-title">üíñ Á¨îËÆ∞Êú¨</div>
			<div style="margin: 10px auto; color: rgba(0,0,0,.78);">ÈöèÊó∂ËÆ∞Á¨îËÆ∞ÔºåÂÖªÊàêËâØÂ•Ω‰π†ÊÉØÔºåÂ∞ÜÊâÄÈÅáÂà∞ÁöÑÈóÆÈ¢òËøõË°åËÆ∞ÂΩïÔºåÊñπ‰æøÂ§ç‰π†Êï¥ÁêÜÔºåÊúâÂà©‰∫éÁü•ËØÜÁöÑÂ∑©Âõ∫!!!</div>
			<a-button type="primary" icon="plus"  @click="showNoteForm">
			      ÂÜôÁ¨îËÆ∞
			</a-button>
		</div>
		
		
		<!-- ÊöÇÊó†Êï∞ÊçÆ -->
		<a-table v-if="noteList.length == 0 && searchData" :data-source="[]" style="margin: 0 10px;"/>		
		
		<!-- Âä†ËΩΩ‰∏≠ -->
		<div class="loading" v-show="!showData">
			<a-spin size="large" :spinning="!showData"/>
		</div>
		
		<a-modal
		      title="ÂÜôÁ¨îËÆ∞"
		      :visible="visible"
		      :confirm-loading="confirmLoading"
			  :width="width"
			  :footer="null"
		    >
			  
			  <a-form-model ref="ruleForm" :model="form" :rules="rules" :label-col="labelCol" :wrapper-col="wrapperCol"
			  	labelAlign="left">
			  	<a-form-model-item ref="name" label="ÂêçÁß∞" prop="name">
			  		<a-input v-model="form.name" placeholder="ÈÅáÂà∞ÁöÑÈóÆÈ¢òÂêçÁß∞ÊàñËÄÖËÆ®ÂéåÁöÑbug"
			  			@blur="  () => { $refs.name.onFieldBlur();} " />
			  	</a-form-model-item>
			  	<a-form-model-item ref="answer" label="Á≠îÊ°à" prop="answer">
			  		<a-input v-model="form.answer" placeholder="Áî®ÁÆÄÊ¥ÅÁöÑËØ≠Ë®ÄËß£Á≠îÈóÆÈ¢òÊàñËÄÖËß£ÂÜ≥ÊñπÊ≥ï" type="textarea"
			  			style="height: 100px; resize: none;"
			  			@blur="  () => { $refs.answer.onFieldBlur();} " />
			  	</a-form-model-item>
			  	<a-form-model-item ref="url" label="Áõ∏ÂÖ≥ÈìæÊé•" prop="url">
			  		<a-input v-model="form.url" placeholder="ÁΩë‰∏äËß£ÂÜ≥ÁöÑÊñπÊ≥ïÊàñËÄÖÁü•ËØÜÁÇπÁõ∏ÂÖ≥ÂÆòÁΩë"
			  			@blur="  () => { $refs.url.onFieldBlur();} " />
			  	</a-form-model-item>
			  	<a-form-model-item ref="other"  prop="other"label="ÂÖ∂‰ªñ">
			  		<a-input v-model="form.other" placeholder="ÂÖ∂‰ªñÁõ∏ÂÖ≥Áü•ËØÜ" type="textarea"
			  			style="height: 50px; resize: none;" />
			  	</a-form-model-item>
				
				<a-form-model-item :wrapper-col="wrapperCol1">
					<a-button type="primary" style="width:40%;min-width: 64px;" @click="handleOk">
						Êèê‰∫§
					</a-button>
					<a-button style="margin-left: 30px;" @click="handleCancel">
						ÂèñÊ∂à
					</a-button>
				</a-form-model-item>
				
			  </a-form-model>
		</a-modal>
		
		<!-- ÈóÆÈ¢òÁ¨îËÆ∞ÂàóË°® -->
		<a-row v-show="showData">
			<a-col v-for="(item,index) in noteList" :xs="24" :sm="24" :md="24" :lg="12" :xl="8">
				<Card :noteItem="item" @deleteNote="deleteNote(item.id)" @editNote="editNote(item)"></Card>
			</a-col>
		</a-row>
		
		<!-- ÂàÜÈ°µ -->
		<div style="text-align: right;margin-right: 10px; padding-bottom: 10px;" v-show="showData && noteList.length > 0">
			<a-pagination :default-current="1" :defaultPageSize="9" :total="total" @change="pagesChange"/>
		</div>
		
	</div>
</template>

<script>
import Card from './card.vue'
export default {
	data(){
		return {
			visible:false,
			confirmLoading:false,
			width:'40%',
			value:'',
			showData:false,
			searchData:false,
			
			labelCol: {
				sm: {
					span: 12,
				},
				md: {
					span: 8,
					offset: 0
				},
				lg: {
					span: 5,
					offset: 0
				},
			},
			wrapperCol: {
				sm: {
					span: 24,
				},
				md: {
					span: 16,
					offset: 0
				},
				lg: {
					span: 19,
					offset: 0
				},
			},
			wrapperCol1: {
				sm: {
					span: 24,
					offset: 0
				},
				md: {
					span: 16,
					offset: 8
				},
				lg: {
					span: 19,
					offset: 5
				},
			},
			
			editId:'',
			// Á¨îËÆ∞‰ø°ÊÅØ
			form: {
				name:'',
				answer:'',
				url:'',
				other:''
			},
			total:0,
			// Ëé∑ÂèñÂàóË°®Êù°‰ª∂
			query:{
				search:'',
				page_size:9,
				page_current:1
			},
			// Á¨îËÆ∞Êï∞ÊçÆÂàóË°®
			noteList:[],
			rules: {
				name: [{
					required: true,
					message: 'ËØ∑Â°´ÂÜôÁ¨îËÆ∞ÂêçÁß∞Êàñbug',
					trigger: 'change'
				}, ],
				answer: [{
					required: true,
					message: 'ËØ∑Â°´ÂÜôËß£ÂÜ≥ÊñπÊ≥ï',
					trigger: 'change'
				}],
				// other: [{
				// 	required: true,
				// 	message: 'ËØ∑Â°´ÂÜôËØ¶ÁªÜ‰ªãÁªçÂèäËØ¥Êòé',
				// 	trigger: 'change'
				// }],
				// url: [{
				// 		required: true,
				// 		message: 'ËØ∑Â°´ÂÜôÁΩëÁ´ôÈìæÊé•',
				// 		trigger: 'change'
				// 	},
				// 	// { validator: url_validate, message: 'ËØ∑Â°´ÂÜôÊ≠£Á°ÆÂüüÂêçÊ†ºÂºè', trigger: 'change' },
				// ],
				}
				
		}
	},
	components:{
		Card
	},
	mounted() {
		// Ëé∑ÂèñÁ¨îËÆ∞ÂàóË°®
		this.getNoteList()
		
		let width = document.documentElement.clientWidth
		// console.log(width)
		if(width < 1200){
			this.width = '450px'
		}else if(width>=1200 && width<1800){
			this.width = '40%'
		}else{
			this.width = '510px'
		}
		
		window.addEventListener('resize',()=>{
			let width = document.documentElement.clientWidth
			// console.log(width)
			if(width < 1200){
				this.width = '450px'
			}else if(width>=1200 && width<1800){
				this.width = '40%'
			}else{
				this.width = '510px'
			}
		})
	},
	methods:{
		showNoteForm(){
			this.editId = 0
			this.visible = true
			this.form = {}
		},
		// Ëé∑ÂèñÁ¨îËÆ∞ÂàóË°®
		getNoteList() {
			let that = this
			this.axios.get('/notes',{
					params:this.query,
				}).then(res => {
					console.log(res)
					if (res.status == 1) {
						this.noteList = res.data.data
						this.total = res.data.total
						this.showData = true
					} else {
						this.$message.error(res.info)
					}
				})
				.catch(err => {
					console.log(err)
				})
		},
		// ÂàõÂª∫Á¨îËÆ∞ Êàñ ÁºñËæë
		handleOk(){
			if(this.editId){
				this.axios.put(`note/${this.editId}`, {
					params: this.form
				}).then(res => {
					console.log(res)
					if (res.status == 1) {
						// console.log(res.data)
						this.$message.success(res.info)
						this.visible = false
						this.getNoteList()
					} else {
						this.$message.error(res.info)
					}
				})
			}else{
				this.$refs.ruleForm.validate(valid => {
					if (valid) {
						console.log(this.form)
						// return
						this.axios.post('note', {
							params: this.form
						}).then(res => {
							console.log(res)
							if (res.status == 1) {
								// console.log(res.data)
								this.$message.success(res.info)
								this.visible = false
								this.getNoteList()
							} else {
								this.$message.error(res.info)
							}
						})
					} else {
						console.log('error submit!!');
						return false;
					}
				});
			}
		},
		// Ê†πÊçÆidÂà†Èô§Á¨îËÆ∞
		deleteNote(id){
			this.axios.delete(`note/${id}`)
			 .then(res => {
				console.log(res)
				if (res.status == 1) {
					// console.log(res.data)
					this.$message.success(res.info)
					this.getNoteList()
				} else {
					this.$message.error(res.info)
				}
			})
		},
		// ÁºñËæëÁ¨îËÆ∞‰ø°ÊÅØ
		editNote(item){
			this.visible = true
			this.editId = item.id
			this.form = item
		},
		// ÂàÜÈ°µ
		pagesChange(page,pagesize){
			// console.log(page,pagesize)
			this.query.page_current = page
			this.getNoteList()
		},
		handleCancel(){
			this.visible = false
			// this.$refs.ruleForm.resetFields();
		}
	}
}
</script>

<style scoped>
	.ant-note{
		/* width: 100%; */
		background-color: #FFFFFF;
		padding: 20px;
		margin: 0 0 20px 10px;
	}
	.ant-note-title{
		font-size: 20px;
		font-weight: 600;
		color: #000000;
	}
	.loading {
	  text-align: center;
	  background: rgba(0, 0, 0, 0.05);
	  border-radius: 4px;
	  margin-bottom: 20px;
	  padding: 30px 50px;
	  margin: 20px 0;
	}
</style>
